.AddStaffModal {
    position: absolute;
    top: calc(50%);
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    height: 75%;
    background: #f9f9f9; /* Un fond légèrement gris pour être doux pour les yeux */
    overflow: auto;
    WebkitOverflowScrolling: touch;
    border-radius: 10px;
    outline: none;
    box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.3);
    border: none;
    font-family: 'Arial', sans-serif;
    scrollbar-width: thin;
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
}

.modal-date-input, .modal-file-input {
    cursor: pointer;
}

hr {
    margin: 4px 0px 0px 0px; 
}

.modal-header {
    position: relative;
    height: 38px;
}

.modal-header h3 {
    margin: 0;
    padding: 10px 0px 0px 0px;
    font-size: 1.5em;
    color: #333;
    text-align: center;
}

.modal-exit-button {
    position: absolute;
    top: 0px;
    right: 0px;
    cursor: pointer;
    background-color: transparent;
    border: none;
    padding: 5px;
}

    .modal-exit-button img {
        width: 20px;
        height: auto;
    }


.modal-body {
    display: flex;
    height: 78%;
    flex-direction: row;
}

.left-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 60%;
    padding: 10px; /* Réduit l'espace autour du contenu */
    background-color: #f0f0f0; /* Change la couleur de fond */
}

.left-section-division {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;

}

.left-section label {
    font-size: 1em; /* Réduit la taille de la police des labels */
    margin-bottom: 5px; /* Réduit l'espace en dessous des labels */
}

.modal-select-input {
    width: 80%;
    font-size: 12px;
}

.right-section {
    flex: 1;
    display: flex;
    align-items: center;
    flex-direction: column;
    border-left: 1px solid #ccc;
    padding: 10px;
    background-color: #f0f0f0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.right-section h3 {
    color: #333;
    font-size: 1.5em;
    margin-bottom: 10px;
    text-align: center;
}

.right-section label {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-start;
    align-items: center; /* Aligne le centre du texte avec le centre de la checkbox */
    margin-bottom: 15px;
    font-size: 1.2em;
    color: #666;
}

.right-section label input[type="checkbox"] {
    order: 2;
    margin-left: 10px;
    transform: scale(1.2);
}

.right-section label span {
    order: 1;
}

.right-section input,
.right-section textarea {
    padding: 5px; /* Réduit l'espace à l'intérieur des inputs et textareas */
    border: 1px solid #ccc; /* Ajoute une bordure autour des inputs et textareas */
    border-radius: 4px; /* Arrondit les coins des inputs et textareas */
    margin-bottom: 10px; /* Réduit l'espace en dessous des inputs et textareas */
    width: 100%; /* Fait en sorte que les inputs et textareas prennent toute la largeur disponible */
}

.modal-footer {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
}

.description {
    flex-grow: 1;
}

.send-button {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #4CAF50; /* Couleur de fond du bouton */
    color: white; /* Couleur du texte du bouton */
    padding: 15px 32px; /* Espacement intérieur du bouton */
    text-align: center; /* Alignement du texte du bouton */
    text-decoration: none; /* Suppression du soulignement du texte du bouton */
    display: inline-block;
    font-size: 16px; /* Taille du texte du bouton */
    margin: 4px 2px; /* Marge autour du bouton */
    cursor: pointer; /* Change le curseur en une main lorsque vous survolez le bouton */
    border-radius: 4px; /* Arrondit les coins du bouton */
}